import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'

/**
 * 用户登录
 */

export function loginByPhone(query) {
  	return fetch({
	    url: publicConst.proxyPath+'login',
	    method: 'get',
	    params:query,
  	});
}



