function filterTomorrowFestival(value) {

	var month = value.getMonth();
	var date = value.getDate();

	if (month == 0 && date == 0) {
		return "明天是元旦节";
	}
	if (month == 1 && date == 12) {
		return "明天是除夕";
	}
	if (month == 1 && date == 13) {
		return "明天是春节/情人节";
	}
	if (month == 2 && date == 0) {
		return "明天是国际海豹日";
	}
	if (month == 2 && date == 7) {
		return "明天是妇女节";
	}
	if (month == 2 && date == 11) {
		return "明天是植树节";
	}
	if (month == 3 && date == 0) {
		return "明天是愚人节";
	}
	if (month == 3 && date == 4) {
		return "明天是清明节";
	}
	if (month == 4 && date == 0) {
		return "明天是劳动节";
	}
	if (month == 4 && date == 8) {
		return "明天是母亲节";
	}
	if (month == 5 && date == 0) {
		return "明天是儿童节";
	}
	if (month == 5 && date == 25) {
		return "明天是禁毒日";
	}
	if (month == 7 && date == 0) {
		return "明天是建军节";
	}
	if (month == 7 && date == 15) {
		return "明天是七夕";
	}
	if (month == 11 && date == 23) {
		return "明天是平安夜";
	}
	if (month == 11 && date == 24) {
		return "明天是圣诞节";
	}
}

