import {
  	loginByPhone,
} from 'api/login';

import {
  	getToken,
  	setToken,
  	removeToken,
  	getUserInfo,
  	setUserInfo,
  	removeUserInfo,
  	getUsers,
  	setUsers,
  	removeUsers
} from 'utils/auth';
import {
	leftMenuPort
} from 'api/model/leftMenu';   //左菜单数据
import dateFormat from 'utils/dateFormat'

const user = {
  	state: {
	    user:'' ,
	    token:getToken(),
	    roles: getUserInfo(),
	    menuData: [], // 菜单元素
	    elements: undefined, // 可操作元素
	    permissionMenus: undefined,  // 权限菜单
	    setting: {},
  	},
  	mutations: {
	    // 更新全局--token
	    SET_TOKEN: (state, token) => {
	      	state.token = token;
	    },
	   
	// 更新全局--角色
	    SET_ROLES: (state, roles) => {
	      state.roles = roles;
	    },
	  // 更新左边菜单权限控制
        SET_MENU: (state, menuData) => {
            state.menuData = menuData
        },

	   
  	},
	
  	actions: {
	    // 手机登录
	    LoginByPhone({
	      	commit
	    }, userInfo) {
	    	 
	      	const username = userInfo.txtuid.trim( ) ;
	      	commit('SET_TOKEN','')
	      	commit('SET_ROLES', '');
	      	commit('SET_MENU', '');
	      	removeToken();
	      	return new Promise((resolve, reject) => {
		        loginByPhone({txtuid:username,txtpwd:userInfo.txtpwd}).then(response => {
		        	console.log('登录' +  typeof response)
		        	if(response.TYPE === 'E'){
		        		resolve(response)
		        	}else{
		        		setUserInfo(response);
		        		setUsers(userInfo.txtuid)
			          	setToken(userInfo.txtuid+'_'+response.TOKEN)
	                    console.log('getUserInfo():::'+getUserInfo())
			          	commit('SET_TOKEN', userInfo.txtuid+'_'+response.TOKEN);
//			          	console.log('setToken:::::'+ setToken(userInfo.txtuid+'_'+response.TOKEN))
//			          	console.log('getToken:::::'+ getToken())
			          	resolve(response)
			          	let req = {
				    		USER_ID:getUsers(),  // 
				    		RIGHTS:getUserInfo().RIGHTS,
			    	    }
		               
		                leftMenuPort(req).then(data => {
		                    commit('SET_MENU',data)
		                }).catch(error => {
		                    reject(error)
		                })
		        	}
		          	
		        }).catch(error => {
		          	reject(error);
		        });
	      	});
	    },
	    
	// 左菜单权限
        LeftMenuPort({
            commit,
            state
        }) {
            return new Promise((resolve, reject) => {
             

            });
        },

	
	    // 登出
//	    LogOut({
//	      	commit
//	    }) {
//	      	return new Promise((resolve, reject) => {
//		        logout().then(() => {
//		          	commit('SET_TOKEN', '');
//		          	commit('SET_ROLES', []);
//		          	commit('SET_MENUS', undefined);
//		          	commit('SET_ELEMENTS', undefined);
//		          	commit('SET_PERMISSION_MENUS', undefined);
//		          	removeToken();
//		          	removeShopowner();
//		          	resolve();
//		        }).catch(error => {
//		          	reject(error);
//		        });
//	      	});
//	    },
//	
	    // 前端 登出
//	    FedLogOut({
//	      	commit
//	    }) {
//	      	return new Promise(resolve => {
//		        commit('SET_TOKEN', '');
//		        commit('SET_MENUS', undefined);
//		        commit('SET_ELEMENTS', undefined);
//		        commit('SET_PERMISSION_MENUS', undefined);
//		        removeToken();
//		        resolve();
//	      	});
//	    },
	    
  	}
};

export default user;



