// creatRole_list
export default [
    {
        path: '/systemUser_list',
        name: 'systemUser_list',
        meta: {
            title: 'systemUser'
        },
        component: ()=>import('../views/systemUser/systemUser.vue')
    },
    {
        path: '/addEditUser_list',
        name: 'addEditUser_list',
        meta: {
            title: '新增人员'
        },
        component: ()=>import('../views/systemUser/addEditUser.vue')
    },
    {
        path: '/addEditUser_list/:id',
        name: 'addEditUser01_list',
        meta: {
            title: '编辑人员'
        },
        component: ()=>import('../views/systemUser/addEditUser.vue')
    }
]



