import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'
import {
	getToken,
} from 'utils/auth';
/**
 * 左菜单
 */

export function leftMenuPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'getmenu',
	    method: 'get',
	    params:query,
  	});
}







