import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import dashboard from '../router/dashboard.js'
import authority from '../router/authority.js'
import menuManage from '../router/menuManage.js'
import systemUser from '../router/systemUser.js'
import rightsObject from '../router/rightsObject.js'
/* 取用户token信息
 */
import {
    getToken,
    getUserInfo
} from 'utils/auth'

/**
 * vuex全局状态引用
 */
import { mapGetters } from 'vuex'
const vuexGet = {
    ...mapGetters([
        'token'
    ])
}
/**
 * 路由判断是否有权限
 */
const requireAuth = (to, from, next) => {
	
		if(getToken()){
			next()
		}else{
	        next({
	             path: '/login',
	             query: {
	                 redirect: to.fullpath
	             }
	        })
				     
		}
		next()	
	}

    

/**
 * 路由数据添加meta属性
 * @param {Object} arr
 * @param {Object} name
 */
function addModelName(arr, name) {
    arr.forEach(item => {
        if (item.meta) {
            item.meta.model = name
        }
    })
    return arr
}

/**
 * 读取路由配置
 */
const views = {
    path: '/',
    //  component: resolve => require(['views/layout/Layout.vue'], resolve).default,
    component: () =>
        import ('views/layout/Layout.vue').then(m => m.default),
    //  beforeEnter: requireAuth,
    
    children: [
        ...addModelName(dashboard, 'dashboard'),
        ...addModelName(authority, 'authority'),
        ...addModelName(menuManage, 'menuManage'),
        ...addModelName(systemUser, 'systemUser'),
         ...addModelName(rightsObject, 'rightsObject'),
        
    ]
}

/**
 * 添加授权处理，父级组件
 */
views.children.forEach(item => {
        item.beforeEnter = requireAuth;
    })
    /**
     * 用户登录路由
     */
const login = {
    path: '/login',
    name: 'login',
    //  component: resolve => require(['../views/login/index.vue'], resolve).default,
    component: () =>
        import ('../views/login/index.vue').then(m => m.default),
}

/**
 * 没有找到路由返回登录页
 */
const noFound = {
    path: '*',
    redirect: to => {
        return '/login'
    }
}
export default new Router({
       	mode: 'hash',
      	base: __dirname,
    routes: [
        login,
        views,
        noFound
    ]
})


