

/**
 * 过滤明天日期是什么节日
 */
export function filterTomorrowFestival(value) {
	
	const month = value.getMonth();
	const date = value.getDate();
  	
  	if ((month == 0) && (date == 0)) {
  		return "明天是元旦节"
  	}
	if ((month == 1) && (date == 12 )){
		return "明天是除夕"
	}
	if ((month == 1) && (date == 13 )){
		return "明天是春节/情人节"
	}
	if ((month == 2) && (date == 0)){
		return "明天是国际海豹日"
	}
	if ((month == 2) && (date == 7)){
		return "明天是妇女节"
	} 
	if ((month == 2) && (date == 11)){
		return "明天是植树节"
	}
	if ((month == 3) && (date == 0)){
		return "明天是愚人节"
	}
	if ((month == 3) && (date == 4)){
		return "明天是清明节"
	} 
	if ((month == 4) && (date == 0)){
		return "明天是劳动节"
	}
	if ((month == 4) && (date == 8)){
		return "明天是母亲节"
	}
	if ((month == 5) && (date == 0)){
		return "明天是儿童节"
	}
	if ((month == 5) && (date == 25)){
		return "明天是禁毒日"
	}
	if ((month == 7) && (date == 0)){
		return "明天是建军节"
	}
	if ((month == 7) && (date == 15)){
		return "明天是七夕"
	}
	if ((month == 11) && (date == 23)){
		return "明天是平安夜"
	}
	if ((month == 11) && (date == 24)){
		return "明天是圣诞节"
	}
	
  	
}


/**
 * 过滤今天是几月几号
 */
export function filterFormatDay(value){
	const month = value.getMonth() + 1;
	const date = value.getDate();
	let currentDay = (month < 10 ? '0'+ month : month) + ' 月 ' + (date < 10 ? '0' + date : date) + '日';
	return currentDay;
}

/**
 * 过滤今天是周几
 */
export function filterFormatWeek(value){
	let show_day = new Array('星期日','星期一','星期二','星期三','星期四','星期五','星期六'); 
	let year = value.getFullYear(); // 当前年份
 	let month = value.getMonth()+1; // 一年中第几个月
 	let date = value.getDate(); // 一个月中第几天
	let day = value.getDay(); // 一周中第几天
 	let hour = value.getHours(); // 当前小时
 	let minutes = value.getMinutes(); // 当前分钟
 	let second = value.getSeconds(); // 当前钞
 	let week = show_day[day]; // 当前是周几
 	return week;
}

/**
 * 过滤今天是当前时间
 */
export function filterFormatTime(value){
 	let hour = value.getHours(); 
 	let minutes = value.getMinutes(); 
 	let second = value.getSeconds(); 
 	let time = (hour < 10 ? '0' + hour : hour) + ':'+  (minutes < 10 ? '0' + minutes : minutes);
 	return time;
}

/**
 * 过滤今天的农历是初几
 */
export function filterGetLunarInfo(value){
	let y = value.getFullYear();
	let m = value.getMonth();
	let d = value.getDate();
    let lunarInfo = calendar.solar2lunar(y,m+1,d)
    let lunarDate = lunarInfo.IDayCn // 农历初几
    let lunarMonth = lunarInfo.IMonthCn // 农历月份
    return lunarMonth + lunarDate
}






