// 
export default [
    {
        path: '/creatRights_list',
        name: 'creatRights_list',
        meta: {
            title: '权限对象创建'
        },
        component: ()=>import('../views/rightsObject/addEditRights.vue')
    },
     {
        path: '/creatRights_list/:id',
        name: 'editRights_list',
        meta: {
            title: '权限对象编辑'
        },
        component: ()=>import('../views/rightsObject/addEditRights.vue')
    },
    {
        path: '/RightsList',
        name: 'RightsList',
        meta: {
            title: '权限对象列表'
        },
        component: ()=>import('../views/rightsObject/rightsList.vue')
    }
    
]



